.svg)}.question__button--wrong::before,.question__button--wrong::after,.question__button--wrong:hover::before,.question__button--wrong:hover::after,.question__button--wrong:hover::before,.question__button--wrong:hover::after,.question__button--wrong:hover:hover::before,.question__button--wrong:hover:hover::after{background-color:#ec6259}.question__button--inactive{pointer-events:none}@media screen and (max-width: 1439px) and (min-width: 992px){.question__button{padding:1.35rem 3.3rem;width:26rem}}@media screen and (max-width: 991px){.question{flex:0 0 100%;padding:13.6rem 0 2.4rem;align-items:center}.question__title{text-align:center}.question__answers{justify-content:center}.question__button{width:28.8rem;margin:0 1.5rem 0.8rem;padding:1.65rem 3.3rem}}@media screen and (max-width: 767px){.question__title{font-size:1.8rem;line-height:2.088rem;max-width:28.8rem}}

